@import 'variables.scss';

.App {
    display: flex;
    width: 100vw;
    max-width: 800px;
    height: 100vh;
    padding: 20px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.input-wrapper {
    width: 100%;
    height: $standard-item-height;
    &__input {
        width: 100%;
        height: 100%;
        border: none;
        border-radius: $br-standard;border-radius: $br-standard;
        padding: 20px;
        font-size: 16px;
        background-color: $dark_veryDarkDesaturatedBlue;
        color: $dark_brightGrayishBlue;
        transition: .3s;
        &.light {
            color: $light-very-dark-grayish-blue;
            background-color: $light-very-light-gray;
        }
    }
}



.options {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    background-color: $dark_veryDarkDesaturatedBlue;
    height: $standard-item-height;
    border-radius: $br-standard;
    transition: .3s;
    &.light {
        background-color: $light-very-light-gray;
    }
    a {
        color: $dark_grayishBlue;
        text-decoration: none;
        font-weight: 700;
        &.active {
            color: $bright-blue;
        }    
    }
    &.light a {
        color: $light-very-dark-grayish-blue;
        &.active {
                color: $bright-blue;
            }
    }
}

